import React from "react"
import { ResumeData } from "../data/ResumeData"

export default class ResumeLayout extends React.Component<{}, {}> {
	public render() {

	
		return(
			<div style={{display: 'flex', height: '100%', backgroundColor: 'white'}}>
				<div id="resume-print" style={{display: 'flex', flexDirection: 'column', margin: 'auto', minWidth: 1000, paddingBottom: 40}}>
					<div style={{margin: '0 auto', marginTop: 30, marginBottom: 20, width: 900}}>
						<div style={{fontSize: '17pt', fontWeight: 600, marginBottom: 5}}>Bryce Egley</div>
						<div style={{display: 'flex', justifyContent: 'space-between', fontSize: '11pt'}}>
							<div>
								<div style={{}}>bryce.a.egley@gmail.com</div>
								<div style={{lineHeight: '0.7em'}}>Aeronautical and Aerospace Engineering - Purdue University</div>
							</div>
							<div style={{textAlign: 'right', display: 'flex', flexDirection: 'column'}}>
								<div style={{marginTop: 'auto'}}> Github:<a href="https://github.com/Mistweaver" style={{textDecoration: 'none', color: 'blue'}}>https://github.com/Mistweaver</a></div>
								<div style={{lineHeight: '0.7em'}}>Website:<a href="http://bryceegley.com" style={{textDecoration: 'none', color: 'blue'}}>www.bryceegley.com</a></div>
							</div>
						</div>
					</div>
					<div style={{margin: '0 auto', width: 900}}>
						{
							ResumeData.map(item => (
								<div key={item.id} className="item">
									<h4 style={{marginBottom: 6}}>{item.role}</h4>
									<h5 style={{marginBottom: 12}}>{item.where}</h5>
									<ul>
										{
											item.details.map(detail => (
												<li key={detail} style={{marginBottom: 0, lineHeight: '1rem'}}>{detail}</li>
											))
										}
										</ul>
								</div>
							))
						}
					</div>
					<div style={{margin: '0 auto', width: 900}}>
						<div style={{fontSize: '13pt', fontWeight: 500}}>Miscellaneous:</div>
						<ul>
							<li style={{marginBottom: 0, lineHeight: '1rem'}}>Specialized in dynamics and control of aerospace systems in college, specifically finite element analysis and kalman filtering.</li>
							<li style={{marginBottom: 0, lineHeight: '1rem'}}>Experience with graphics and rendering in OpenGL, LWJGL, Unity 3D, as well as modeling software like Catia.</li>
							<li style={{marginBottom: 0, lineHeight: '1rem'}}>I played collegiate and semi-professional rugby.</li>
							<li style={{marginBottom: 0, lineHeight: '1rem'}}>I am a published internet writer and sports columnist.</li>
							<li style={{marginBottom: 0, lineHeight: '1rem'}}>I am an avid aquarist and took courses in college covering aquaponics, biology, and marine biology.</li>
							<li style={{marginBottom: 0, lineHeight: '1rem'}}>Highest altitude I have climbed is 14,439ft, have visited over two dozen national parks, and love dispersed camping and fishing.</li>
						</ul>
					</div>
				</div>
			</div>
		)
	}
}